import { dialogsAPI } from '../api/api';

const CREATE_MESSAGE_BODY = 'CREATE_MESSAGE_BODY';
const SET_ID_FOR_MESSAGE = 'SET_ID_FOR_MESSAGE';
const SET_INTERLOCUTORS = 'SET_INTERLOCUTORS';
const SET_MESSAGES_DATA = 'SET_MESSAGES_DATA';
const SET_RESPONSE_MESSAGE = 'SET_RESPONSE_MESSAGE';

let initialState = {
  userIdForMessage: '',
  messagesData: null,
  responseMessage: null,
  interlocutors: null,
};

const dialogsReducer = (state = initialState, action) => {
  switch (action.type) {
    case SET_ID_FOR_MESSAGE: {
      return {
        ...state,
        userIdForMessage: action.userIdForMessage,
      };
    }
    case SET_INTERLOCUTORS: {
      return { ...state, interlocutors: action.data };
    }
    case SET_MESSAGES_DATA: {
      return { ...state, messagesData: action.items };
    }
    case SET_RESPONSE_MESSAGE: {
      return { ...state, responseMessage: action.message };
    }
    default:
      return state;
  }
};

export const createMessageBody = (newMessageBody) => ({
  type: CREATE_MESSAGE_BODY,
  newMessageBody,
});

export const sendMessage = (newMessageBody, userId) => async (dispatch) => {
  try {
    const response = await dialogsAPI.sendMessage(newMessageBody, userId);
    dispatch(setResponseMessage(response));
  } catch {
    dispatch(setResponseMessage('Request error'));
  }
};

const setResponseMessage = (message) => ({
  type: SET_RESPONSE_MESSAGE,
  message,
});

const setMessagesData = (items) => ({
  type: SET_MESSAGES_DATA,
  items,
});

export const getDialogsWithFriend = (userId) => async (dispatch) => {
  const response = await dialogsAPI.getDialogsWithFriend(userId);
  dispatch(setMessagesData(response.data.items));
  dispatch(setIdForMessage(userId));
};

export const setIdForMessage = (userIdForMessage) => ({
  type: SET_ID_FOR_MESSAGE,
  userIdForMessage,
});
export const getAllDialogs = () => async (dispatch) => {
  const response = await dialogsAPI.getAllDialogs();
  dispatch(setDialogs(response.data));
};

const setDialogs = (data) => ({
  type: SET_INTERLOCUTORS,
  data,
});

export const startChatting = (userId) => async (dispatch) => {
  const response = await dialogsAPI.startChatting(userId);
};

export default dialogsReducer;
